.contactItem {
  width: 400px;
  display: flex;
  justify-content: space-between;
  gap: 8px;
  font-size: 15px;
  font-weight: 300;
  color: rgb(25, 25, 25);
}

.contactButton {
  width: 65px;
  height: 25px;
  border: none;
  border-radius: 5px;
  background-color: #fdc780;
  -webkit-box-shadow: 2px 2px 8px -6px rgba(68, 68, 68, 1);
  -moz-box-shadow: 2px 2px 8px -6px rgba(68, 68, 68, 1);
  box-shadow: 2px 2px 8px -6px rgba(68, 68, 68, 1);

  font-size: 14px;
  font-weight: 300;
  color: white;
  cursor: pointer;
}

.contactButton:hover {
  background-color: rgb(255, 157, 0);
  color: white;
}
