.contactForm {
  width: 300px;
  margin-bottom: 20px;
  padding: 20px;

  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 12px -9px rgba(0, 0, 0, 1);
  -moz-box-shadow: 0px 0px 12px -9px rgba(0, 0, 0, 1);
  box-shadow: 0px 0px 12px -9px rgba(0, 0, 0, 1);

  background-color: rgb(255, 247, 220);
}

.contactLabel {
  margin-bottom: 15px;

  font-size: 17px;
  font-weight: 200;
  color: rgb(25, 25, 25);

  display: flex;
  flex-direction: column;
  gap: 5px;
}

.contactInput {
  height: 25px;
  border: 1px solid rgb(190, 190, 190);
  border-radius: 5px;

  font-size: 15px;
  font-weight: 200;
  color: rgb(0, 0, 0);
}

.contactInput:hover,
:focus,
:focus-visible {
  border: 2px solid #fdc780;
  outline: none;
  -webkit-box-shadow: 0px 0px 15px -2px rgba(255, 181, 0, 1);
  -moz-box-shadow: 0px 0px 15px -2px rgba(255, 181, 0, 1);
  box-shadow: 0px 0px 15px -2px rgba(255, 181, 0, 1);
}

.contactButton {
  width: 100px;
  height: 25px;
  border: none;
  border-radius: 5px;
  background-color: #fdc780;
  -webkit-box-shadow: 2px 2px 8px -6px rgba(68, 68, 68, 1);
  -moz-box-shadow: 2px 2px 8px -6px rgba(68, 68, 68, 1);
  box-shadow: 2px 2px 8px -6px rgba(68, 68, 68, 1);

  font-size: 14px;
  font-weight: 300;
  color: white;
  cursor: pointer;
}

.contactButton:hover {
  background-color: rgb(255, 157, 0);
}
